'use client';

import { useState } from "react";
import { useUser } from "@clerk/nextjs";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { toast } from "sonner";
import { UserProfile } from "@clerk/nextjs";

type TotpState =
  | { status: "idle" }
  | { status: "created"; secret?: string; uri?: string }
  | { status: "verified" };

export default function MfaPage() {
  const { isLoaded, user } = useUser();
  const [totp, setTotp] = useState<TotpState>({ status: "idle" });
  const [code, setCode] = useState("");
  const [isWorking, setIsWorking] = useState(false);
  const [backupCodes, setBackupCodes] = useState<string[] | null>(null);

  if (!isLoaded || !user) {
    return null;
  }

  const u = user as NonNullable<typeof user>;
  const mfaEnabled = u.twoFactorEnabled;

  async function handleCreateTotp() {
    try {
      setIsWorking(true);
      const res = await u.createTOTP();
      setTotp({ status: "created", secret: res.secret, uri: res.uri });
      toast.success("Authenticator setup started. Scan the QR code and verify.");
    } catch (err) {
      toast.error("Failed to start authenticator setup");
    } finally {
      setIsWorking(false);
    }
  }

  async function handleVerifyTotp(e: React.FormEvent) {
    e.preventDefault();
    if (!code) return;
    try {
      setIsWorking(true);
      await u.verifyTOTP({ code });
      setTotp({ status: "verified" });
      setCode("");
      toast.success("Authenticator app verified and MFA enabled");
      // Optionally generate backup codes right away
      const bc = await u.createBackupCode();
      setBackupCodes(bc.codes);
    } catch (err) {
      toast.error("Invalid code. Try again.");
    } finally {
      setIsWorking(false);
    }
  }

  async function handleDisableTotp() {
    try {
      setIsWorking(true);
      await u.disableTOTP();
      setTotp({ status: "idle" });
      setBackupCodes(null);
      toast.success("MFA has been disabled");
    } catch (err) {
      toast.error("Failed to disable MFA");
    } finally {
      setIsWorking(false);
    }
  }

  async function handleGenerateBackupCodes() {
    try {
      setIsWorking(true);
      const bc = await u.createBackupCode();
      setBackupCodes(bc.codes);
      toast.success("New backup codes generated");
    } catch (err) {
      toast.error("Failed to generate backup codes");
    } finally {
      setIsWorking(false);
    }
  }

  return (
    <div className="mx-auto flex w-full max-w-2xl flex-col gap-6 p-4">
      <div className="rounded-2xl border border-gray-200 p-6 shadow-2xl">
        <h1 className="mb-2 text-xl font-semibold">Multi‑Factor Authentication</h1>
        <p className="mb-4 text-sm text-gray-600">
          Add an extra layer of security to your account using an authenticator app and backup codes.
        </p>

        {mfaEnabled ? (
          <div className="space-y-6">
            <div className="rounded-lg border bg-gray-50 p-4">
              <p className="text-sm">
                Status: <span className="font-medium text-green-700">Enabled</span>
              </p>
            </div>

            <div className="flex flex-wrap gap-3">
              <Button variant="destructive" disabled={isWorking} onClick={handleDisableTotp}>
                Disable MFA
              </Button>
              <Button disabled={isWorking} onClick={handleGenerateBackupCodes}>
                Generate backup codes
              </Button>
            </div>

            {backupCodes && (
              <div className="rounded-lg border p-4">
                <p className="mb-2 text-sm font-medium">Backup codes (store securely):</p>
                <ul className="grid grid-cols-2 gap-2 text-sm font-mono">
                  {backupCodes.map((c) => (
                    <li key={c} className="rounded border bg-white px-2 py-1">
                      {c}
                    </li>
                  ))}
                </ul>
              </div>
            )}
          </div>
        ) : (
          <div className="space-y-6">
            {totp.status === "idle" && (
              <div className="space-y-4">
                <p className="text-sm text-gray-700">
                  Use an authenticator app (like Google Authenticator, 1Password, or Authy).
                </p>
                <Button onClick={handleCreateTotp} disabled={isWorking}>
                  Set up authenticator
                </Button>
              </div>
            )}

            {totp.status === "created" && (
              <div className="space-y-5">
                {totp.uri && (
                  <div className="flex flex-col items-center gap-3">
                    <img
                      alt="Scan this QR in your authenticator app"
                      className="h-40 w-40 rounded-md border"
                      src={`https://api.qrserver.com/v1/create-qr-code/?size=200x200&data=${encodeURIComponent(
                        totp.uri
                      )}`}
                    />
                    <p className="text-xs text-gray-500">Scan the QR with your authenticator app</p>
                  </div>
                )}
                {totp.secret && (
                  <div className="rounded-md border bg-gray-50 p-3">
                    <p className="text-xs text-gray-500">Or enter this secret manually:</p>
                    <p className="font-mono text-sm">{totp.secret}</p>
                  </div>
                )}

                <form onSubmit={handleVerifyTotp} className="space-y-3">
                  <div className="space-y-2">
                    <Label htmlFor="code">Enter 6‑digit code</Label>
                    <Input
                      id="code"
                      inputMode="numeric"
                      pattern="[0-9]*"
                      maxLength={6}
                      value={code}
                      onChange={(e) => setCode(e.target.value)}
                      placeholder="123456"
                    />
                  </div>
                  <div className="flex gap-3">
                    <Button type="submit" disabled={isWorking || code.length !== 6}>
                      Verify and enable
                    </Button>
                    <Button
                      type="button"
                      variant="ghost"
                      onClick={() => setTotp({ status: "idle" })}
                      disabled={isWorking}
                    >
                      Cancel
                    </Button>
                  </div>
                </form>
              </div>
            )}

            {totp.status === "verified" && (
              <div className="space-y-4">
                <p className="text-sm text-green-700">MFA is now enabled for your account.</p>
                {backupCodes && (
                  <div className="rounded-md border p-3">
                    <p className="mb-2 text-sm font-medium">Backup codes (store securely):</p>
                    <ul className="grid grid-cols-2 gap-2 text-sm font-mono">
                      {backupCodes.map((c) => (
                        <li key={c} className="rounded border bg-white px-2 py-1">
                          {c}
                        </li>
                      ))}
                    </ul>
                  </div>
                )}
              </div>
            )}
          </div>
        )}
      </div>

      <div className="rounded-2xl border border-gray-200 p-6 shadow-2xl">
        <h2 className="mb-2 text-lg font-semibold">Manage other security settings</h2>
        <p className="mb-4 text-sm text-gray-600">
          You can also review sessions, change password, and more.
        </p>
        <UserProfile
          routing="hash"
          __experimental_startPath="security"
          appearance={{
            elements: { card: "shadow-2xl border border-gray-200 rounded-2xl" },
          }}
        />
      </div>
    </div>
  );
}


